import { Field, InputType } from "@nestjs/graphql";
import { IsEmail, IsNotEmpty, IsOptional, IsString } from "class-validator";


@InputType()
export class LoginClientUsingEmailInput {

  @Field(() => String)
  @IsEmail()
  @IsOptional()
  @IsNotEmpty()
  email: string;

  @Field(() => String)
  @IsNotEmpty()
  @IsString()
  password: string;
  

}
